* {
  align-items: center;
  justify-content: center;
  position: relative;
}

.Square {
  width: 100px;
  height: 100px;
  border: 0;
  background-color: #fff;
  color: #374151;
  font-size: 2rem;
  border: 1px solid #d2d6dc;
  font-weight: bold;
}

.Square:hover {
  cursor: pointer;
}

.Board {
  font-family: sans-serif;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Row {
  display: flex;
}

.Reset {
  margin-top: 1rem;
  border: 1px solid #d2d6dc;
  color: #374151;
  font-size: 1.25rem;
  padding: 1rem 4rem;
  border-radius: 0.5rem;
  background-color: #fff;
}

.Reset:hover {
  cursor: pointer;
}
